import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { HashRouter, Redirect, Route, Switch } from 'react-router-dom';
import { SettingsView } from './base/SettingsView';
import { TopBar } from './base/TopBar';
import { CreateSurveyView } from './create-survey/CreateSurveyView';
import { IntlWrapper } from './i18n/IntlWrapper';
import { ListSurveysView } from './list-surveys/ListSurveysView';
import * as serviceWorker from './serviceWorker';
import { store } from './store/root';

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <IntlWrapper>
        <HashRouter>
          <div style={{ fontFamily: `sans-serif` }}>
            <TopBar />
            <hr />

            <Switch>
              <Route exact path={`/settings`}>
                <SettingsView />
              </Route>

              <Route exact path={`/create-survey`}>
                <CreateSurveyView />
              </Route>

              <Route exact path={`/list-surveys`}>
                <ListSurveysView />
              </Route>

              <Route path="*">
                <Redirect to={`/list-surveys`} />
              </Route>
            </Switch>
          </div>
        </HashRouter>
      </IntlWrapper>
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
