import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { formatISO } from 'date-fns';
import { v4 as uuidv4 } from 'uuid';
import { generateSurveys } from '../_debug/generateSurveys';

export type SurveyId = string;

interface Survey {
  title: string;
  tsCreated: string;
  tsUnlock: string;
  tsExpiry: string;
}

export type Surveys = Record<SurveyId, Survey>;

const initialState: Surveys = process.env.REACT_APP_DEMO_MODE
  ? generateSurveys()
  : {};

const surveysSlice = createSlice({
  name: `surveys`,
  initialState,
  reducers: {
    surveysCreateOne: (
      state,
      action: PayloadAction<{
        title: string;
        tsUnlock: Date;
        tsExpiry: Date;
      }>
    ) => {
      const { title, tsUnlock, tsExpiry } = action.payload;

      return {
        ...state,
        [uuidv4()]: {
          title,
          tsCreated: formatISO(new Date()),
          tsUnlock: formatISO(tsUnlock),
          tsExpiry: formatISO(tsExpiry),
        },
      };
    },
    surveysDeleteOne: (
      state,
      action: PayloadAction<{ surveyId: SurveyId }>
    ) => {
      const updatedListOfSurveys: Surveys = { ...state };
      delete updatedListOfSurveys[action.payload.surveyId];
      return updatedListOfSurveys;
    },
    surveysClearAll: () => {
      return {};
    },
  },
});

export const {
  surveysCreateOne,
  surveysDeleteOne,
  surveysClearAll,
} = surveysSlice.actions;

export const { reducer } = surveysSlice;
