import * as React from 'react';
import { useIntl } from 'react-intl';
import { SURVEY_STATUS_KEY, useSurveyStatusDescriptions } from './surveyStatus';
import { DisplayOptions } from './useDisplayableSurveys';

export const useStatusFilterConfigurator = (
  options: DisplayOptions,
  updateOptions: (newValue: DisplayOptions) => void
) => {
  const { formatMessage } = useIntl();
  const surveyStatuses = useSurveyStatusDescriptions();
  const hiddenStatuses = new Set(options.hiddenStatuses);

  const statusFilterView = (
    <div>
      <h5 style={{ marginBottom: 8 }}>
        {formatMessage({ id: `list-surveys.status-filter-header` })}
      </h5>

      {Object.entries(surveyStatuses).map(([statusKey, statusDescription]) => {
        const typedStatusKey = statusKey as SURVEY_STATUS_KEY;

        return (
          <div key={statusKey}>
            <label style={{ display: `inline-block` }}>
              <input
                type={`checkbox`}
                onChange={() => {
                  const currentlyToggledOn = hiddenStatuses.has(typedStatusKey);
                  if (currentlyToggledOn) {
                    hiddenStatuses.delete(typedStatusKey);
                  } else {
                    hiddenStatuses.add(typedStatusKey);
                  }

                  updateOptions({
                    ...options,
                    hiddenStatuses: Array.from(hiddenStatuses),
                  });
                }}
                checked={hiddenStatuses.has(typedStatusKey)}
              />
              <span style={{ marginLeft: 8 }}>{statusDescription}</span>
            </label>
          </div>
        );
      })}
    </div>
  );

  return {
    statusFilterView,
  };
};
