import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { useHistory } from 'react-router-dom';
import { withSearchParameter } from '../base/withSearchParameter';
import { SurveysTable } from './SurveysTable';
import { DisplayOptions, useDisplayableSurveys } from './useDisplayableSurveys';
import { useHideFilteredItems } from './useHideFilteredItemsConfigurator';
import { useStatusFilterConfigurator } from './useStatusFilterConfigurator';

const ViewActual: React.FunctionComponent<{
  parameterValue: DisplayOptions;
  updateParameterValue: (newValue: DisplayOptions) => void;
}> = ({
  parameterValue: displayOptions,
  updateParameterValue: updateDisplayOptions,
}) => {
  const history = useHistory();
  const displayableSurveys = useDisplayableSurveys(displayOptions);
  const { statusFilterView } = useStatusFilterConfigurator(
    displayOptions,
    updateDisplayOptions
  );
  const { hideFilteredItemsToggle } = useHideFilteredItems(
    displayOptions,
    updateDisplayOptions
  );

  return (
    <React.Fragment>
      <h2 style={{ display: `flex` }}>
        <FormattedMessage id={`list-surveys.header`} />
        <button
          style={{ marginLeft: 16 }}
          onClick={() => {
            history.push(`/create-survey`);
          }}
        >
          <FormattedMessage id={`create-survey.header`} />
        </button>
      </h2>

      {statusFilterView}
      {hideFilteredItemsToggle}

      <SurveysTable surveys={displayableSurveys} />
    </React.Fragment>
  );
};

export const ListSurveysView = withSearchParameter<DisplayOptions>(
  ViewActual,
  `display`,
  {}
);
