import { formatISO, isValid, parseISO } from 'date-fns';
import * as React from 'react';
import { useIntl } from 'react-intl';
import { LabelledWrapper } from './LabelledWrapper';

interface DateInputConfig {
  isRequired: boolean;
  isDisabled: boolean;
}

const useDateInput = ({ isRequired, isDisabled }: DateInputConfig) => {
  const [inputValue, setInputValue] = React.useState<Date>();
  const [minDate, setMinDate] = React.useState<Date>();
  const [maxDate, setMaxDate] = React.useState<Date>();

  const inputField = (
    <input
      type={`date`}
      required={isRequired}
      disabled={isDisabled}
      min={minDate ? formatISO(minDate, { representation: `date` }) : undefined}
      max={maxDate ? formatISO(maxDate, { representation: `date` }) : undefined}
      value={
        inputValue === undefined
          ? ``
          : formatISO(inputValue, { representation: `date` })
      }
      onChange={(event) => {
        const parsedDate = parseISO(event.target.value);
        if (isValid(parsedDate)) {
          setInputValue(parsedDate);
        }
      }}
    />
  );

  return {
    inputValue,
    inputField,
    setMinDate,
    setMaxDate,
  };
};

interface DateRangeInputConfig extends DateInputConfig {
  labelId: string;
  allowPast: boolean;
}

export const useDateRangeInput = ({
  labelId,
  isRequired,
  isDisabled,
  allowPast,
}: DateRangeInputConfig) => {
  const { formatMessage } = useIntl();
  const {
    inputValue: startDate,
    inputField: startDateInput,
    setMinDate: setMinDateForStart,
    setMaxDate: setMaxDateForStart,
  } = useDateInput({
    isRequired,
    isDisabled,
  });
  const {
    inputValue: endDate,
    inputField: endDateInput,
    setMinDate: setMinDateForEnd,
  } = useDateInput({
    isRequired,
    isDisabled: isDisabled || startDate === undefined,
  });

  React.useEffect(() => {
    if (!allowPast) {
      setMinDateForStart(new Date());
    }
  }, [allowPast, setMinDateForStart]);

  React.useEffect(() => {
    setMaxDateForStart(endDate);
  }, [endDate, setMaxDateForStart]);

  React.useEffect(() => {
    setMinDateForEnd(startDate);
  }, [setMinDateForEnd, startDate]);

  const dateRangeInput = (
    <LabelledWrapper labelText={formatMessage({ id: labelId })}>
      {startDateInput}
      <span style={{ margin: `0 8px` }}>
        {formatMessage({ id: `arrow-right` })}
      </span>
      {endDateInput}
    </LabelledWrapper>
  );

  return {
    startDate,
    endDate,
    dateRangeInput,
  };
};
