import * as React from 'react';
import { Redirect, useHistory, useLocation } from 'react-router-dom';

export function withSearchParameter<T>(
  Component: React.FunctionComponent<{
    parameterValue: T;
    updateParameterValue: (newValue: T) => void;
  }>,
  parameterKey: string,
  defaultValue: T
): React.FunctionComponent {
  const defaultValueSerialized = JSON.stringify(defaultValue);

  return () => {
    const location = useLocation();
    const history = useHistory();
    const queryParams = new URLSearchParams(location.search);

    const updateParameterValue = (newValue: T) => {
      queryParams.set(parameterKey, JSON.stringify(newValue));
      history.push(`${location.pathname}?${queryParams.toString()}`);
    };

    try {
      const valueSerialized = queryParams.get(parameterKey);

      if (valueSerialized === null) {
        throw new Error(
          `Cannot find a value for "${parameterKey}" in the query-parameters; resetting to the default value (${defaultValueSerialized})!`
        );
      }

      const parameterValue = JSON.parse(valueSerialized);

      return (
        <Component
          parameterValue={parameterValue}
          updateParameterValue={updateParameterValue}
        />
      );
    } catch (e) {
      console.warn(e);

      queryParams.set(parameterKey, defaultValueSerialized);
      return (
        <Redirect
          to={{
            pathname: location.pathname,
            search: queryParams.toString(),
          }}
        />
      );
    }
  };
}
