import * as React from 'react';
import { useIntl } from 'react-intl';
import { LabelledWrapper } from './LabelledWrapper';

interface SelectConfig<T extends string> {
  labelId: string;
  options: Record<T, string>;
  initialValue: T;
}

export function useSelect<T extends string>({
  labelId,
  options,
  initialValue,
}: SelectConfig<T>) {
  const { formatMessage } = useIntl();
  const [selectedValue, setSelectedValue] = React.useState(initialValue);

  const selectField = (
    <LabelledWrapper labelText={formatMessage({ id: labelId })}>
      <select
        onChange={(event) => setSelectedValue(event.currentTarget.value as T)}
        value={selectedValue}
      >
        {Object.entries<string>(options).map(
          ([optionKey, optionDescription]) => {
            return (
              <option key={optionKey} value={optionKey}>
                {optionDescription}
              </option>
            );
          }
        )}
      </select>
    </LabelledWrapper>
  );

  return {
    selectedValue,
    selectField,
  };
}
