import { addDays, formatISO, getYear, isFuture, subDays } from 'date-fns';
import { Surveys } from '../store/surveys';
import { dataGenerator } from './dataGenerator';

const now = new Date();
const thisYear = getYear(now);

export const generateSurveys = (): Surveys => {
  const numSurveys = dataGenerator.natural({ max: 25 });

  console.log(
    `Generating ${numSurveys} surveys with the following seed... ${dataGenerator.seed}`
  );

  const output: Surveys = {};
  for (let i = 0; i < numSurveys; i++) {
    const tsUnlock = dataGenerator.date({ year: thisYear }) as Date;
    const tsExpiry = addDays(tsUnlock, dataGenerator.natural({ max: 100 }));
    const tsCreated = subDays(tsUnlock, dataGenerator.natural({ max: 25 }));

    output[dataGenerator.guid()] = {
      title: dataGenerator.company(),
      tsCreated: formatISO(isFuture(tsCreated) ? now : tsCreated),
      tsUnlock: formatISO(tsUnlock),
      tsExpiry: formatISO(tsExpiry),
    };
  }

  return output;
};
