import * as React from 'react';

export const LabelledWrapper: React.FunctionComponent<{
  labelText: string;
}> = ({ labelText, children }) => {
  return (
    <label style={{ display: `block`, marginTop: 24 }}>
      <div style={{ marginBottom: 8 }}>{labelText}</div>
      {children}
    </label>
  );
};
