import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import { useDispatch } from 'react-redux';
import { surveysDeleteOne } from '../store/surveys';
import { DisplayableSurvey } from './useDisplayableSurveys';

export const SurveyActions: React.FunctionComponent<{
  survey: DisplayableSurvey;
}> = ({ survey }) => {
  const dispatch = useDispatch();

  if (survey.status === `new`) {
    return (
      <button
        onClick={() => {
          dispatch(surveysDeleteOne({ surveyId: survey._id }));
        }}
      >
        <FormattedMessage id={`list-surveys.action-delete-label`} />
      </button>
    );
  }

  return null;
};
