import * as React from 'react';
import { useIntl } from 'react-intl';
import { DisplayOptions } from './useDisplayableSurveys';

export const useHideFilteredItems = (
  options: DisplayOptions,
  updateOptions: (newValue: DisplayOptions) => void
) => {
  const { formatMessage } = useIntl();

  const hideFilteredItemsToggle = (
    <label style={{ display: `inline-block`, margin: `16px 0` }}>
      <input
        type={`checkbox`}
        checked={options.hideFiltered || false}
        onChange={() => {
          updateOptions({
            ...options,
            hideFiltered: !options.hideFiltered,
          });
        }}
      />
      <span style={{ marginLeft: 8 }}>
        {formatMessage({ id: `list-surveys.hide-filtered-items-label` })}
      </span>
    </label>
  );

  return { hideFilteredItemsToggle };
};
