import * as React from 'react';
import { useIntl } from 'react-intl';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { surveysCreateOne } from '../store/surveys';
import { useActiveDateRangeInput } from './useActiveDateRangeInput';
import { useTitleInput } from './useTitleInput';

export const CreateSurveyView: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();
  const history = useHistory();
  const dispatch = useDispatch();
  const [showGenericError, setShowGenericError] = React.useState(false);
  const { titleValue, titleInput } = useTitleInput();
  const { dateRangeInput, startDate, endDate } = useActiveDateRangeInput();

  React.useEffect(() => {
    setShowGenericError(false);
  }, [titleValue, startDate, endDate]);

  const handleSubmit: React.FormEventHandler = (event) => {
    event.preventDefault();

    if (
      titleValue === undefined ||
      startDate === undefined ||
      endDate === undefined
    ) {
      return setShowGenericError(true);
    }

    dispatch(
      surveysCreateOne({
        title: titleValue,
        tsUnlock: startDate,
        tsExpiry: endDate,
      })
    );

    history.push(`/list-surveys`);
  };

  return (
    <React.Fragment>
      <h2 style={{ display: `flex` }}>
        <button onClick={() => history.push(`/`)}>
          {formatMessage({ id: `arrow-left` })}
        </button>
        <span style={{ marginLeft: 16 }}>
          {formatMessage({ id: `create-survey.header` })}
        </span>
      </h2>

      <form onSubmit={handleSubmit}>
        {titleInput}
        {dateRangeInput}

        <div style={{ marginTop: 32 }}>
          <button type={`submit`}>
            {formatMessage({ id: `create-survey.submit` })}
          </button>
        </div>

        {showGenericError && (
          <div style={{ color: `red` }}>
            {formatMessage({ id: `create-survey.generic-error` })}
          </div>
        )}
      </form>
    </React.Fragment>
  );
};
