import * as React from 'react';
import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { LOCALES, LOCALE_KEY } from '../i18n/locale';
import { selectSettings } from '../store/root';
import { settingsUpdateLocale } from '../store/settings';
import { surveysClearAll } from '../store/surveys';
import { DebugView } from '../_debug/DebugView';
import { useSelect } from './useSelect';

export const SettingsView: React.FunctionComponent = () => {
  const { formatMessage } = useIntl();
  const history = useHistory();
  const { locale: activeLocale } = useSelector(selectSettings);
  const dispatch = useDispatch();
  const {
    selectedValue: selectedLocale,
    selectField: localeSelect,
  } = useSelect({
    labelId: `settings.locale`,
    options: LOCALES,
    initialValue: activeLocale,
  });

  React.useEffect(() => {
    dispatch(settingsUpdateLocale(selectedLocale as LOCALE_KEY));
  }, [dispatch, selectedLocale]);

  return (
    <React.Fragment>
      <h2 style={{ display: `flex` }}>
        <button onClick={() => history.push(`/`)}>
          {formatMessage({ id: `arrow-left` })}
        </button>
        <span style={{ marginLeft: 16 }}>
          {formatMessage({ id: `settings` })}
        </span>
      </h2>

      {localeSelect}

      <button
        onClick={() => dispatch(surveysClearAll())}
        style={{ marginTop: 16 }}
      >
        {formatMessage({ id: `settings.clear-surveys` })}
      </button>

      <hr />
      <DebugView />
    </React.Fragment>
  );
};
