import { parseISO } from 'date-fns';
import { useSelector } from 'react-redux';
import { selectAllSurveys } from '../store/root';
import { SurveyId } from '../store/surveys';
import { deriveSurveyStatus, SURVEY_STATUS_KEY } from './surveyStatus';

export type StatusFilter = Array<SURVEY_STATUS_KEY>;

export interface DisplayOptions {
  hideFiltered?: boolean;
  hiddenStatuses?: StatusFilter;
}

type SurveyVisibility = `show` | `hide` | `deemphasize`;

export interface DisplayableSurvey {
  _id: SurveyId;
  title: string;
  tsCreated: Date;
  tsUnlock: Date;
  tsExpiry: Date;
  status: SURVEY_STATUS_KEY;
  visibility: SurveyVisibility;
}

const isFilteredOutByStatus = (
  surveyStatus: SURVEY_STATUS_KEY,
  hiddenStatuses?: StatusFilter
) => {
  if (hiddenStatuses) {
    return hiddenStatuses.includes(surveyStatus);
  }

  return false;
};

export const useDisplayableSurveys = ({
  hideFiltered,
  hiddenStatuses,
}: DisplayOptions): Array<DisplayableSurvey> => {
  const surveys = useSelector(selectAllSurveys);
  const visibilityForFilteredItems: SurveyVisibility = hideFiltered
    ? `hide`
    : `deemphasize`;

  return Object.entries(surveys).map(([surveyId, surveyDetails]) => {
    const { title, tsCreated, tsUnlock, tsExpiry } = surveyDetails;

    const tsUnlockDate = parseISO(tsUnlock);
    const tsExpiryDate = parseISO(tsExpiry);

    const surveyStatus = deriveSurveyStatus(tsUnlockDate, tsExpiryDate);
    let visibility: SurveyVisibility = `show`;
    if (hiddenStatuses) {
      if (isFilteredOutByStatus(surveyStatus, hiddenStatuses)) {
        visibility = visibilityForFilteredItems;
      }
    }

    return {
      _id: surveyId,
      title,
      tsCreated: parseISO(tsCreated),
      tsUnlock: tsUnlockDate,
      tsExpiry: tsExpiryDate,
      status: surveyStatus,
      visibility,
    };
  });
};
