import { isPast } from 'date-fns';
import { useIntl } from 'react-intl';

export type SURVEY_STATUS_KEY = `new` | `in-progress` | `completed`;

export const useSurveyStatusDescriptions = (): Record<
  SURVEY_STATUS_KEY,
  string
> => {
  const { formatMessage } = useIntl();

  return {
    new: formatMessage({ id: `survey-status.new` }),
    'in-progress': formatMessage({ id: `survey-status.in-progress` }),
    completed: formatMessage({ id: `survey-status.completed` }),
  };
};

export const deriveSurveyStatus = (
  tsUnlock: Date,
  tsExpiry: Date
): SURVEY_STATUS_KEY => {
  if (isPast(tsExpiry)) {
    return `completed`;
  }

  if (isPast(tsUnlock)) {
    return `in-progress`;
  }

  return `new`;
};
