import * as React from 'react';
import { IntlProvider } from 'react-intl';
import { useSelector } from 'react-redux';
import { Loading } from '../base/Loading';
import { selectSettings } from '../store/root';
import { LOCALE_KEY } from './locale';

const loadLocaleMessages = (locale: LOCALE_KEY) => {
  switch (locale) {
    case `es`:
      return import('./es.json');
    default:
      return import('./en.json');
  }
};

export const IntlWrapper: React.FunctionComponent = ({ children }) => {
  const { locale } = useSelector(selectSettings);
  const [messages, setMessages] = React.useState<Record<string, string>>();

  React.useEffect(() => {
    (async () => {
      const loadedMessages = await loadLocaleMessages(locale);
      setMessages(loadedMessages.default);
    })();
  }, [locale]);

  if (messages) {
    return (
      <IntlProvider
        messages={messages}
        locale={locale}
        defaultLocale={`en`}
        onError={(err) => {
          // TODO Re-enable errors for missing translations.
          if (err.code === `MISSING_TRANSLATION`) {
            return;
          }

          throw err;
        }}
      >
        {children}
      </IntlProvider>
    );
  }

  return <Loading />;
};
