import { formatDistanceToNowStrict } from 'date-fns';
import * as React from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { SurveyActions } from './SurveyActions';
import { useSurveyStatusDescriptions } from './surveyStatus';
import { DisplayableSurvey } from './useDisplayableSurveys';

const TABLE_CELL_STYLES = {
  border: `solid 1px black`,
  padding: 8,
};

const TABLE_HEADER_MSG_IDS = [
  `list-surveys.title-header`,
  `list-surveys.status-header`,
  `list-surveys.actions-header`,
  `list-surveys.creation-date-header`,
  `list-surveys.unlock-date-header`,
  `list-surveys.expiry-date-header`,
];

const TableRowNoDataWarning: React.FunctionComponent = () => {
  return (
    <tr>
      <td colSpan={TABLE_HEADER_MSG_IDS.length} style={TABLE_CELL_STYLES}>
        <FormattedMessage id={`list-surveys.empty-table`} />
      </td>
    </tr>
  );
};

const SurveysTableDataRow: React.FunctionComponent<{
  survey: DisplayableSurvey;
}> = ({ survey }) => {
  const { formatMessage } = useIntl();
  const statusDescriptions = useSurveyStatusDescriptions();
  const emDash = formatMessage({ id: `em-dash` });
  const { title, tsCreated, tsUnlock, tsExpiry, status, visibility } = survey;
  const [tsCreatedRelative, setTsCreatedRelative] = React.useState<string>(
    emDash
  );
  const [tsUnlockRelative, setTsUnlockRelative] = React.useState<string>(
    emDash
  );
  const [tsExpiryRelative, setTsExpiryRelative] = React.useState<string>(
    emDash
  );

  const updateRelativeTimestamps = React.useCallback(() => {
    setTsCreatedRelative(
      formatDistanceToNowStrict(tsCreated, {
        addSuffix: true,
      })
    );
    setTsUnlockRelative(
      formatDistanceToNowStrict(tsUnlock, {
        addSuffix: true,
      })
    );
    setTsExpiryRelative(
      formatDistanceToNowStrict(tsExpiry, {
        addSuffix: true,
      })
    );
  }, [tsCreated, tsExpiry, tsUnlock]);

  React.useEffect(() => {
    updateRelativeTimestamps();

    // Periodically update the relative timestamps:
    const interval = setInterval(updateRelativeTimestamps, 1000);
    return () => clearInterval(interval);
  }, [tsCreated, updateRelativeTimestamps]);

  const rowStyles: React.CSSProperties = {
    visibility: visibility === `hide` ? `collapse` : `visible`,
  };
  if (visibility === `deemphasize`) {
    rowStyles.opacity = 0.15;
    rowStyles.backgroundColor = `lightgrey`;
  }

  return (
    <tr style={rowStyles}>
      <td style={TABLE_CELL_STYLES}>{title}</td>
      <td style={TABLE_CELL_STYLES}>{statusDescriptions[status]}</td>
      <td style={TABLE_CELL_STYLES}>
        <SurveyActions survey={survey} />
      </td>
      <td style={TABLE_CELL_STYLES}>{tsCreatedRelative}</td>
      <td style={TABLE_CELL_STYLES}>{tsUnlockRelative}</td>
      <td style={TABLE_CELL_STYLES}>{tsExpiryRelative}</td>
    </tr>
  );
};

export const SurveysTable: React.FunctionComponent<{
  surveys: Array<DisplayableSurvey>;
}> = ({ surveys }) => {
  const { formatMessage } = useIntl();

  return (
    <table style={{ border: `solid 1px black`, borderCollapse: `collapse` }}>
      <thead>
        <tr>
          {TABLE_HEADER_MSG_IDS.map((id, index) => (
            <th key={index} style={TABLE_CELL_STYLES}>
              {formatMessage({ id })}
            </th>
          ))}
        </tr>
      </thead>
      <tbody>
        {surveys.length > 0 ? (
          surveys.map((survey) => (
            <SurveysTableDataRow key={survey._id} survey={survey} />
          ))
        ) : (
          <TableRowNoDataWarning />
        )}
      </tbody>
    </table>
  );
};
